@import "variables";
@import "theme";
@import "../../grid/variables";
@import "../../font";
@import "../../mixins";

.u-tab-bar {
  position: relative;
  display: flex;
  flex-direction: row;
  width: 100%;
  height: $u-tab-height;
}

.u-tab {
  display: flex;
  flex: 1;
  flex-direction: row;
  align-items: center;
  padding-right: $u-tab-horizontal-padding;
  padding-left: $u-tab-horizontal-padding;
  min-width: $u-tab-min-width;
  max-width: $u-tab-max-width;
  font-family: $u-font-family-base;
  cursor: pointer;
  @include selectable-background-before;
}

.u-tab-indicator {
  position: absolute;
  bottom: 0;
  height: $u-tab-indicator-height;
  content: "";
  transition: 350ms cubic-bezier(.35, 0, .25, 1);
}

.u-tab-content {
  width: 100%;
  text-align: center;
}

.u-tab-text {
  position: relative;
  display: inline-block;
  font-size: $u-tab-font-size;
  font-weight: $u-tab-font-weight;
  text-transform: uppercase;
}

@include media-breakpoint-up(md) {

  .u-tab {
    flex: none;
  }
}

.u-tab-bar-scrollable {
  justify-content: flex-start;

  .u-tab {
    flex: none;

    &:first-child {
      margin-left: $u-tab-bar-scrollable-margin;
    }
  }
}
